import { notFound } from 'next/navigation';
import { mockVehicles } from '@/data/mockData';
import { VehicleDetailsClient } from '@/components/VehicleDetailsClient';

interface VehiclePageProps {
  params: {
    id: string;
  };
}

export default function VehiclePage({ params }: VehiclePageProps) {
  const vehicle = mockVehicles.find(v => v.id === params.id);

  if (!vehicle) {
    notFound();
  }

  return <VehicleDetailsClient vehicle={vehicle} />;
}
